
:root {
  --red  : #FF526C;
  --yellow :  #FFE261;
  --gray : #E2E1E7;
}

.h1,.h2,.h3,.h4,.h5,.h6{ 

  color: var(--red);
}

body{

  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  /* font-family:Georgia, 'Times New Roman', Times, serif ; */
  margin: 0;
  padding: 0;
  color: var(--red);
  top: 0;
  background-color: var(--gray);
}


/* navbar */

.navbar{

  width: 100%;
  /* border: 1px solid; */
  top: 0;
  /* position: fixed; */
  padding-top: 10px;
  height: auto;
  z-index: 2;
  background-color: var(--yellow);


}

.navbar-toggler{

  border: 0.5px solid var(--red);
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar-toggler img{

  width: 20px;
  height: 20px;
  object-fit: cover;
}

#main-nav{

  /* border: 1px solid; */
  background-color: var(--yellow);
  

}

#logo{

  width: 40px;
  height:40px;
  z-index: 2;
}

a span{

  font-size: small;
  color: var(--red);
  font-weight: 600;
}


a span:hover{

  border-bottom: var(--red) 2px solid;
  /* color: var; */
   

}


/* Home */

#home{

  background: url('./assets/images/home.jpg');
  background-repeat: no-repeat;
  background-position: 0% 50%;
  background-size:1000px;
  height: 563px;
  /* position: relative; */
  z-index: 1;
  background-color: var(--gray);
  display: flex;
  justify-content:center;
  align-items: center;
  flex-direction: row;
  /* border: 1px solid red; */
  width: 100%;
  overflow: hidden;
}

.home-container{

  display: flex;
  justify-content: flex-end;
  align-items: center;
  /* border: 1px solid yellow; */
  width: 1300px;
}


.home-container .contained{

  /* border: 1px solid green; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 500px;
  width: 500px;
  
}

.home-container .contained a{


  text-decoration: none;
  color: white;
  background-color: var(--red);
  width: 230px;
  text-align: center;
  /* height: 30px; */
  border-radius: 5px;
  font-size: 20px;
  border: 1px solid;
  
}
.home-container .contained a:hover{

  color: var(--red);
  background-color: var(--gray);
  border: 1px solid var(--red);

}



#home-logo{

  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  /* border: 1px solid black; */
  /* margin: 10px; */
  
}

.big-logo-container{

  width: 200px;
  height: 200px;
  margin: 20px;
}

.big-logo-container img{

  width: 100%;
  height: 100%;
}

#home .contained .home-line{

  border: 2px solid var(--yellow);
  width: 400px;
  margin: 10px;
}

#home .contained .lead{

  color: rgb(0, 0, 0);
  font-weight: 600;

}


/* browse */

#browse{

  min-height:700px;
  display: flex;
  justify-content: center;
  align-items: center;


}

#browse .container-xxl{

  display: flex;
  justify-content: flex-start;
  align-items: center;
  /* border: 1px solid; */
  flex-direction: column;
  height: 98%;
}

.search-input{

  /* border: 1px solid; */
  margin: 30px;

}

.search-input input{

  width: 400px;
  height: 40px;
  background-color: var(--yellow);
  border: none;
  border-radius: 5px;
}

.search-input input::placeholder{

  text-align: center;
  color: white;
}

.content{

  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid; */
  width: 98%;
}

#loading{

  width: 200px;
  height: 200px;
  border-top: var(--red) 5px solid;
  border-bottom: var(--yellow) 5px solid;
  border-radius: 50%;
  animation-name: loadingAnimation;
  animation-duration:1s;
  animation-iteration-count: infinite;

}



@keyframes loadingAnimation {

0% {transform: rotate(0deg);}
10% {transform: rotate(60deg)}
20% {transform: rotate(120deg)}
30% {transform: rotate(150deg)}
40% {transform: rotate(180deg)}
50% {transform: rotate(210deg)}
60% {transform: rotate(240deg)}
70% {transform: rotate(270deg)}
80% {transform: rotate(300deg)}
90% {transform: rotate(330deg)}
100% {transform: rotate(360deg)}





}



