/* payment */

#payment{

  height: 600px;
  width: 100%;
  /* border: 1px solid; */
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

}

#payment-container{

  /* border: 1px solid; */
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}

#payment-container h1{

  text-align: center;
}

.payment-top-line{

  border: 2px solid var(--yellow);
  width: 60%;
}

.payment-images{

  width: 80%;
  /* border: 1px solid; */
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.creditCard, .shield{

  width: 30%;
  height: 80%;
  /* border: 1px solid; */
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.creditCard img, .shield img{

  width: 70%;
  height: 100%;
  object-fit: contain;
}

.payment-bottom-line{

  border: 2px solid var(--yellow);
  width: 80%;

}

.paymentMethods{

  width: 100%;
  /* border: 1px solid; */
  display: flex;
  justify-content: center;
  align-items: center;

}

.paymentImages{

  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid green; */
  width: 100%;

}

.paymentImg{

  width: 60px;
  height: 60px;
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid; */
  border-radius: 50%;
  background-color: var(--yellow);
  transition: all 0.2s linear;
}

.paymentImg:hover{

  box-shadow: 0px 5px 5px gray;
  transform:translateY(-5px);

}

.paymentImg img{

  width: 100%;
  height: 100%;
  object-fit: cover;
}

